export const testCase_3 = `
    <p style="text-align: center; line-height: 3;"><span style="font-size: 40px;"><strong>第一批知识产权代理行业“蓝天”专项整治行动警示案例发布（1-8）</strong></span></p><p style="text-align: center; line-height: 3;"><br></p><p style="text-align: center; line-height: 3;"><br></p><p style="text-indent: 2em; text-align: justify; line-height: 3;">2019年以来，国家知识产权局在全国范围内持续深入开展知识产权代理行业“蓝天”专项整治行动，组织各地重拳打击专利、商标代理违法违规行为，截至目前，共约谈专利、商标代理机构7400余家，责令整改4500余家，作出警告和罚款处罚680余件，作出吊销代理资质、停止代理业务等重处罚20件，罚没金额3200余万元，在行业内引起强烈反响，形成了全国范围内严厉打击违法违规代理行为的持续高压态势，行业秩序明显好转。</p><p style="text-align: justify; line-height: 3;"><br></p><p style="text-indent: 2em; text-align: justify; line-height: 3;">国家知识产权局将其中一些案例汇集，发布第一批“蓝天”专项整治行动警示案例，集中在商标代理方面，包括代理不以使用为目的恶意抢注涉新冠疫情防控、奥运热词等商标申请，伪造、变造公文、签名或者史料造假等行为，接受同一商标案件中利益冲突双方委托、虚假宣传、诋毁同行等以不正当手段招揽业务等违法行为。专利、商标代理机构和从业人员要引以为戒，自觉守法规范经营，共同营造良好行业环境。也提醒广大专利、商标申请人提高警惕，选择信誉良好的专利、商标代理机构，维护自身合法权益。</p><p style="text-align: justify; line-height: 3;"><br></p><p style="text-align: justify; line-height: 3;"><span style="font-size: 29px;"><strong>第一批警示案例1—8详情如下：</strong></span></p><p style="text-align: justify; line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>案例 1 &nbsp; &nbsp;上海某知识产权代理有限公司代理抢注“火神山”“雷神山”商标案</strong></span></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><strong>新冠疫情发生以来，一些申请人为了蹭热度，抢注诸如“火神山”“雷神山”等与疫情防控相关的名称作为商标，个别商标代理机构受利益驱动，代理相关商标注册申请，扰乱行业秩序，造成不良社会影响，受到严厉处罚。</strong></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>基本案情</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">2020年1月29日，当事人与上海某国际贸易有限公司商定，由其代理申请注册“火神山”“雷神山”商标。2020年2月3日，当事人向国家知识产权局提交注册申请。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>法律依据及处罚</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">上海市市场监督管理局依据《中华人民共和国商标法》第六十八条第一款第（三）项的规定，给予当事人责令改正，警告，罚款8.5万元的处罚。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>案例 2 &nbsp; &nbsp;北京某知识产权代理有限公司代理抢注“冰墩墩”“雪容融”商标案</strong></span></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><strong>代理机构代理恶意抢注北京2022年冬奥会和冬残奥会吉祥物相关商标，受到严厉处罚。</strong></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>基本案情</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">北京2022年冬奥会和冬残奥会组委会于2019年9月16日向国家知识产权局申请了“冰墩墩”“雪容融”商标。2019年9月25日，裴某委托当事人申请注册“冰墩墩”“雪容融”商标。当事人于2019年9月26日代理提交了上述商标注册申请。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><strong>法律依据及处罚</strong></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">北京市市场监督管理局依据《中华人民共和国商标法》第六十八条第一款第（三）项的规定，给予当事人责令改正，警告，罚款8万元的处罚。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>案例 3 &nbsp; &nbsp;怀化市某知识产权代理有限公司代理抢注“全红婵、红婵”商标案</strong></span></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><strong>少数代理机构代理恶意抢注包括奥运健儿在内的具有较高知名度的公众人物姓名，受到严厉处罚。</strong></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>基本案情</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">当事人于2021年8月10日，接受注册“全红婵、红婵”商标的委托代理申请。于2021年8月16日，向国家知识产权局提交“全红婵、红婵”商标的注册申请。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>法律依据及处罚</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">湖南省怀化市市场监督管理局依据《中华人民共和国商标法》第六十八条第一款第（三）项，给予当事人警告，罚款1.5万元的处罚；对直接负责主管人员给予罚款5000元的处罚。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>案例 4 &nbsp; &nbsp;台州某知识产权代理有限公司“快递到付”案</strong></span></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><strong>商标代理机构以快递邮寄商标注册信息的形式诱使收件人到付邮资，以“快递到付”的形式诱使收件人签收以招揽商标代理业务，图谋不当利益，受到应有处罚。</strong></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>基本案情</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">2021年9月起，台州某知识产权代理有限公司及其法定代表人黄某通过收集企业注册商标信息，制作自行设计格式的“商标终止公告”，在台州玉环、安徽阜阳两地通过EMS快递到付的形式向商标注册人邮寄，并在快递寄件人地址上填写北京市西城区茶马街1号国家知识产权局商标局旁，配货信息盖印“商标终止文件”字样，以引人误解的方式诱使收件人签收，以招揽商标代理业务。2021年10月21日至12月6日期间，共计邮寄7064件。案涉“商标终止合同”邮寄后，当事人未与相关企业签订过商标代理合同，无商标代理业务成交。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>法律依据及处罚</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">台州市市场监督管理局依据《中华人民共和国商标法》第六十八条第一款第（二）项的规定，给予当事人责令改正，警告，没收违法所得6723.62元，罚款1.2万元的处罚。给予直接责任人黄某警告，罚款5000元的处罚。国家知识产权局依据《中华人民共和国商标法》第六十八条第一款第（二）项、第六十八条第二款和《中华人民共和国商标法实施条例》第九十条规定，决定永久停止受理当事人商标代理业务；依据《市场监督管理严重违法失信名单管理办法》第二条、第十二条相关规定，决定将当事人列入市场监督管理严重违法失信名单，通过国家企业信用信息公示系统公示，并实施相应管理措施。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>案例 5 &nbsp; &nbsp;江苏某商标代理有限公司囤积商标案</strong></span></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><strong>商标代理机构通过注册关联公司，采取复制、抄袭他人商标等方式申请注册商标，囤积转让以牟取不正当利益，受到严厉处罚。</strong></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>基本案情</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">当事人于2017年4月1日开始从事商标代理业务。为规避商标代理公司不可以申请注册除商标代理服务之外商标的规定，通过授意成立或控制关联公司的方式申请注册商标并转让牟利，2018年1月至2020年3月共申请注册803件商标，其中包括诸多同他人相同或近似的商标，具有明显复制、抄袭他人商标的故意。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>法律依据及处罚</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">常熟市市场监督管理局根据《中华人民共和国商标法》第六十八条第一款的规定，对当事人给予6万元罚款的处罚，并给予直接负责的主管人员3万元罚款的处罚。</p><p style="line-height: 3;">国家知识产权局根据《中华人民共和国商标法》第六十八条第一款第（三）项、第六十八条第二款、《中华人民共和国商标法实施条例》第九十条的规定，决定给予当事人永久停止受理其办理商标代理业务的处罚，并予公告。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>案例 6 &nbsp; &nbsp;广州市某国际品牌管理有限公司恶意注册商标案</strong></span></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><strong>商标代理机构代理提交相同商品上相同或近似商标的注册申请，损害已注册商标专用权人利益，受到严厉处罚。</strong></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>基本案情</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">截至2020年5月20日，当事人共代理1059件商标注册申请，分属于190个商标申请人，其中58个商标申请人通过在美国科罗拉多州、英国伦敦、中国香港等地登记与已注册商标专用权人名称完全相同、地址却分属不同国家或地区的新公司，在相同商品上提交相同或相近似商标的注册申请。当事人在2019年1月至2020年4月期间，受上述58家公司委托，共向国家知识产权局提出635件商标注册申请，其中部分商标与同名公司注册、使用在先的商标相同或近似，涉及商标数量224个。在此过程中，当事人主管人员罗某与委托公司直接接洽并全程负责。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>法律依据及处罚</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">广州市市场监督管理局依据《中华人民共和国商标法》第六十八条第一款第（三）项的规定，给予当事人责令改正，警告，罚款6万元的处罚。对直接负责的主管人员予以警告，并处罚款2万元的处罚。</p><p style="line-height: 3;">国家知识产权局根据《中华人民共和国商标法》第六十八条第一款、第六十八条第二款、《中华人民共和国商标法实施条例》第九十条的规定，决定给予当事人停止受理其办理商标代理业务十二个月的处罚，并予公告。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>案例 7 &nbsp; &nbsp;上海某商标事务所伪造、变造国家机关公文案</strong></span></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><strong>商标代理机构通过伪造、变造法律公文，非法办理商标事宜，相关机构和人员分别受到行政、刑事处罚。</strong></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>基本案情</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">当事人上海某商标事务所主管吴某为招揽商标代理业务，利用电脑软件，伪造、变造了《商标续展通知书》《商标评审案件答辩通知书》《商标驳回通知书》等8类原商标局公文，并指派两名业务员通过微信和腾讯QQ发给有关企业负责人，两名业务员在发现该主管伪造、变造国家机关公文的事实后，仍接受其指派，使用上述伪造、变造的公文，先后欺骗100家企业和6名个人办理了相关商标代理业务，并收取费用。上述伪造、变造的公文共126份，经鉴定均系伪造或变造。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>法律依据及处罚</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">上海市浦东新区知识产权局依据《中华人民共和国商标法》第六十八条的规定，责令当事人立即改正违法行为，并给予警告、罚款10万元的处罚；分别给予两名相关业务员警告并罚款的处罚；主管吴某因涉嫌犯罪，移送司法机关追究刑事责任。上海市浦东新区人民法院依法作出刑事判决，认定当事人以上罪名成立，判处有期徒刑3年、缓刑5年，并处罚金人民币15000元。国家知识产权局根据《中华人民共和国商标法》第六十八条第一款第（一）项、第六十八条第二款、《中华人民共和国商标法实施条例》第九十条的规定，给予当事人永久停止受理其办理商标代理业务的处罚，并予公告。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>案例 8 &nbsp; &nbsp;北京某知识产权代理有限公司伪造委托人签名案</strong></span></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><strong>商标代理机构通过伪造委托人签名的方式，非法申请注册商标，扰乱商标代理市场秩序，影响委托人利益，受到相应处罚。</strong></p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>基本案情</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">2019年8月8日，当事人在接受林某委托代理范某和钱某的商标转让事宜时，通过商标局网站以电子提交方式提交了商标的转让申请，代理将范某名下商标转让给钱某。后因申请时提交的转、受让双方的商标代理委托书填写有误，被商标局通知补正，当事人经补正于2020年8月18日再次以电子方式提交商标代理委托书，其中委托人范某、钱某的签字均非本人真实签名，而是当事人从2019年8月8日提交的商标代理委托书上剪切、粘贴而成。</p><p style="line-height: 3;"><br></p><p style="line-height: 3;"><span style="background-color: rgb(64, 169, 255);"><strong>法律依据及处罚</strong></span></p><p style="line-height: 3;"><br></p><p style="text-indent: 2em; line-height: 3;">北京市丰台区市场监督管理局依据《中华人民共和国商标法》第六十八条第一款第（一）项的规定，给予当事人责令改正，警告，罚款1万元的处罚。</p>
`